import React, { useState, useEffect } from 'react';
import './CountdownTimerPc.css'; // 確保引入CSS

const NumberSliderPc = ({ value }) => {
    const [prevValue, setPrevValue] = useState(value);
    const [animate, setAnimate] = useState(false);

    useEffect(() => {
        if (value !== prevValue) {
            setAnimate(true);
            setTimeout(() => {
                setPrevValue(value);
                setAnimate(false);
            }, 500); // 动画持续时间
        }
    }, [value, prevValue]);

    return (
        <div className="number-slider">
            <div className={`number-slider-current ${animate ? 'slide-out' : 's1'}`}>
                {prevValue}
            </div>
            <div className={`number-slider-next ${animate ? 'slide-in' : 's2'}`}>
                {value}
            </div>
        </div>
    );
};

const CountdownTimerPc = () => {
    const formatNumber = (number) => {
        return number < 10 ? `0${number}` : number;
    };

    const calculateTimeLeft = () => {
        const difference = +new Date("2024-06-01") - +new Date();
        let timeLeft = {};

        if (difference > 0) {
            timeLeft = {
                D: formatNumber(Math.floor(difference / (1000 * 60 * 60 * 24))),
                H: formatNumber(Math.floor((difference / (1000 * 60 * 60)) % 24)),
                M: formatNumber(Math.floor((difference / 1000 / 60) % 60)),
                S: formatNumber(Math.floor((difference / 1000) % 60))
            };
        }

        return timeLeft;
    };

    const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());

    useEffect(() => {
        const timer = setInterval(() => {
            setTimeLeft(calculateTimeLeft());
        }, 1000);

        return () => clearInterval(timer);
    }, []);

    return (
        <div className="countdown-container">
            <NumberSliderPc value={timeLeft.D} />
            <span className="colon">:</span>
            <NumberSliderPc value={timeLeft.H} />
            <span className="colon">:</span>
            <NumberSliderPc value={timeLeft.M} />
            <span className="colon">:</span>
            <NumberSliderPc value={timeLeft.S} />
        </div>
    );
}

export default CountdownTimerPc;