import Image from 'react-bootstrap/Image';
import CountdownTimerPc from "./CountdownTimerPc";
import CountdownTimerMobile from "../Mobile/CountdownTimerMobile";

export function MainTimeCountPc() {
    return (
        <div className='position-relative' style={{maxWidth: '100%'}}>
            <Image src="timeCountBG.png" style={{width: '100%', height: 'auto'}}/>
            <Image src="images/main/pc/timeCounterBG.png" className='position-absolute' style={{
                width: '30%',
                top: '47%',
                left: '59%',
                transform: 'translate(0%, -50%)',
            }}>
            </Image>
            <div className='position-absolute' style={{
                width: '30%',
                top: '47%',
                left: '59.25%',
                color: "white",
                transform: 'translate(0%, -50%)',
            }}>
                <CountdownTimerPc></CountdownTimerPc>
            </div>
        </div>
    )
}