import {Button} from "react-bootstrap";
import {useAccount} from 'wagmi'
import {useWeb3Modal} from '@web3modal/wagmi/react'

import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

import {useState} from 'react'
import {useContractWrite, usePrepareContractWrite, useWaitForTransaction} from 'wagmi'
import {parseEther} from 'viem'

import cctABI from '../../../../ABI/cctABI.json';

import {Link} from 'react-router-dom';
import Image from "react-bootstrap/Image";

export const NavbarPc = () => {
    const {isDisconnected} = useAccount()
    const {open} = useWeb3Modal()

    function handleButtonClick() {
        open();
    }

    const {data, isLoading, isSuccess, write} = useContractWrite({
        address: '0x43c2fd02e50b5b3c8b289a987dac98ceaf933514',
        abi: cctABI,
        functionName: 'buyCCTWithETH',
    })

    const [inputValue, setInputValue] = useState('')

    const showSwal = () => {
        withReactContent(Swal).fire({
            title: <div>請輸入要花的E數量</div>,
            html: `
                <input id="swal-input" type="number" class="swal2-input" step="0.001" value="0.001" placeholder="輸入數值">
                <div id="swal-output">換算結果：0</div>`,
            inputValue: 1,
            focusConfirm: false,
            preConfirm: () => {
                write({
                    value: parseEther(document.getElementById("swal-input").value.toString())
                });
            },
            didOpen: () => {
                const input = document.getElementById('swal-input');
                const output = document.getElementById('swal-output');

                const convertedValue = convertValue(input.value);
                output.textContent = `換算 CCT：${convertedValue}`;

                input.addEventListener('input', () => {
                    const value = input.value;
                    const convertedValue = convertValue(value);
                    output.textContent = `換算 CCT：${convertedValue}`;
                });
            }
        })
    }

    const convertValue = (value) => {
        return Number((value * 100000).toFixed(2));
    };

    let width = 0;

    if (!isDisconnected)
        width = 10;

    return (
        <div className='position-relative' style={{maxWidth: '100%', height: '4.25vw'}}>
            <div className='position-absolute top-50' style={{
                left: '2.25%',
                transform: 'translate(0%, -50%)',
            }}>
                <Image src="images/logo.png" style={{width: '3vw', height: 'auto'}}/>
            </div>
            <div className='position-absolute top-50' style={{
                right: 42.5 + width + '%',
                transform: 'translate(0%, -50%)',
            }}>
                <Link to="/"
                      style={{color: 'white', fontSize: '1vw', fontWeight: '800', textDecoration: 'none'}}>Home</Link>
            </div>
            <div className='position-absolute top-50' style={{
                right: 30 + width + '%',
                transform: 'translate(0%, -50%)',
            }}>
                <Link to="/" style={{
                    color: 'white',
                    fontSize: '1vw',
                    fontWeight: '800',
                    textDecoration: 'none'
                }}>Staking</Link>
            </div>
            <div className='position-absolute top-50' style={{
                right: 18.5 + width + '%',
                transform: 'translate(0%, -50%)',
            }}>
                <Link to="/about"
                      style={{color: 'white', fontSize: '1vw', fontWeight: '800', textDecoration: 'none'}}>About</Link>
            </div>
            {
                !isDisconnected && (
                    <div className='position-absolute top-50' style={{
                        right: '14.5%',
                        transform: 'translate(0%, -50%)',
                    }}>
                        <Button
                            style={{
                                background: 'red',
                                border: '0',
                                borderRadius: '10vw',
                                width: 'auto',
                                fontSize: '1vw',
                                fontWeight: '800',
                                paddingLeft: '0.5vw',
                                paddingRight: '0.5vw',
                                paddingTop: '0.2vw',
                                paddingBottom: '0.2vw',
                            }}
                            onClick={showSwal}
                        >
                            {isLoading ? 'Buying...' : 'BUY NOW'}
                        </Button>
                    </div>
                )
            }
            <div className='position-absolute top-50' style={{
                right: '3%',
                transform: 'translate(0%, -50%)',
            }}>
                <Button
                    style={{
                        background: '#42a5fe',
                        border: '0',
                        borderRadius: '10vw',
                        width: 'auto',
                        fontSize: '1vw',
                        fontWeight: '800',
                        paddingLeft: '0.5vw',
                        paddingRight: '0.5vw',
                        paddingTop: '0.2vw',
                        paddingBottom: '0.2vw',
                    }}
                    onClick={handleButtonClick}
                >
                    {
                        isDisconnected ? 'Connect' : 'Account'
                    }
                </Button>
            </div>
        </div>
        // <div className='m-2'>
        //     <Row className='align-items-center'>
        //         <Col lg={6} className='ms-2'>
        //             <img
        //                 src="images/logo.png"
        //                 width="79px"
        //                 height="auto"
        //                 className="d-inline-block align-top"
        //                 alt="React Bootstrap logo"
        //             />
        //         </Col>
        //         <Col lg={1}>
        //             <Link to="/" style={{color: 'white', fontSize: '18px', fontWeight: '400'}}>Home</Link>
        //         </Col>
        //         <Col lg={1}>
        //             <Link to="/about" style={{color: 'white', fontSize: '18px', fontWeight: '400'}}>About</Link>
        //         </Col>
        //         <Col>
        //             <w3m-button/>
        //         </Col>
        //         {!isDisconnected && (
        //             <Col>
        //                 <Button
        //                     style={{
        //                         background: 'red',
        //                         border: '0',
        //                         borderRadius: '10vw',
        //                         width: 'auto',
        //                         fontSize: '1vw',
        //                         fontWeight: '800',
        //                         paddingTop: '0.4vw',
        //                         paddingBottom: '0.5vw',
        //                     }}
        //                 >
        //                     BUY NOW
        //                 </Button>
        //             </Col>
        //         )}
        //     </Row>
        // </div>
    )
}