import Image from "react-bootstrap/Image";
import CountdownTimerPc from "../PC/CountdownTimerPc";
import CountdownTimerMobile from "./CountdownTimerMobile";

export function MainTimeCountDownMobile() {
    return (
        <div className='position-relative' style={{maxWidth: '100%'}}>
            <Image src="images/main/mobile/timeCountDownBG.png" style={{width: '100%', height: 'auto'}}/>
            <div className='position-absolute' style={{
                width: '100%',
                height: '7.5vw',
                top: '35%',
                left: '0%',
                color: "white",
                transform: 'translate(0%, -50%)',
            }}>
                <CountdownTimerMobile></CountdownTimerMobile>
            </div>
            <div className='position-absolute w-100' style={{
                textAlign: 'center',
                top: '80%',
                left: '50%',
                transform: 'translate(-50%, 0%)',
                color: '#F4BE35',
                fontSize: '5.25vw',
                fontWeight: '500',
            }}>
                Get Ready to Clown The Fame:<br/>
                Cyber Clown Token!
            </div>
        </div>
    )
}