import Image from "react-bootstrap/Image";

export function WelcomePc() {
    return (
        <div className='position-relative' style={{maxWidth: '100%'}}>
            <Image src="welcomeBG.png" style={{width: '100%', height: 'auto'}}/>
            <div style={{
                position: 'absolute',
                top: '75%',
                textAlign: 'center',
                left: '50%',
                transform: 'translate(-50%, 0%)', // 用于居中
                color: 'white',
                width: '100%',
                fontSize: '1.3vw',
                fontWeight: '500',
            }}>
                Hey there, you fearless cybers voyagers!<br/>
                Buckle up, 'cause we're about to spill the tea on a whole new level of meme-worthy madness.<br/>
                Enter the realm of Cyber Clown Token – the memecoin that's here to roast the roasters and <br/>
                meme the memers.
            </div>
        </div>
    )
}